




import { Component, Prop, Vue } from 'vue-property-decorator'
import { Terminal } from 'xterm'
import * as fullscreen from 'xterm/lib/addons/fullscreen/fullscreen'
import * as attach from 'xterm/lib/addons/attach/attach'
import 'xterm/dist/xterm.css'
import 'xterm/dist/addons/fullscreen/fullscreen.css'
Terminal.applyAddon(fullscreen)
Terminal.applyAddon(attach)

@Component
export default class Console extends Vue {
  @Prop({default: {}}) terminal !:any

  term :any
  terminalSocket :any

  private runRealTerminal() :void {
    console.log('webSocket is finished')
  }
  private errorRealTerminal() :void {
    console.log('error')
  }
  private closeRealTerminal() :void {
    console.log('close')
  }

  mounted() :void {
    console.log('pid : ' + this.terminal.pid + ' is on ready')
    let terminalContainer = document.getElementById('terminal')

    this.term = new Terminal(this.terminal)
    this.term.open(terminalContainer)
    // open websocket
    this.terminalSocket = new WebSocket('ws://127.0.0.1:8901/terminals/')
    this.terminalSocket.onopen = this.runRealTerminal
    this.terminalSocket.onclose = this.closeRealTerminal
    this.terminalSocket.onerror = this.errorRealTerminal
    this.term.attach(this.terminalSocket)
    this.term._initialized = true
    console.log('mounted is going on')
  }
  beforeDestroy() :void {
    this.terminalSocket.close()
    this.term.destroy()
  }

}
